<template>
    <div>
      <div style="float: left;width:50%;margin-left: 6.25%">
        <div class="shadow" v-for="item in articleList" style="width:100%;height:15%;margin-top: 1.5%;border:solid 1px rgba(0,0,0,0.1);float: left;">
          <Icon type="md-heart-outline" title="收藏" size="30" style="float: left;margin-left:2.34%;margin-top: 1.8%;color:#ff5f17;"/>
          <p :title="item.hot" style="float: left;margin-left: -3%;margin-top: 6%;font-size: 16px;color: #ff5f17">{{item.hot}}</p>
          <p @click="showDetail(item.id)" class="href" :title="item.title" style="float: left;margin-left: 2.73%;margin-top: 0.9%;width:75%;overflow:hidden;text-overflow:ellipsis;white-space: nowrap;">{{item.title}}</p>
          <p title="这是内容" style="float: left;margin-left: 3%;margin-top: 2%;width:70%;font-size: 16px;color:#657180;overflow:hidden;text-overflow:ellipsis;text-align: left;white-space: nowrap;">{{item.content}}</p>
          <p style="float: left;margin-left:-70%;margin-top: 7%;font-size: 15px;color: #657180">发布人：{{item.author}}</p>
          <p style="float: left;margin-left: 10%;margin-top: 2%;font-size: 15px;color: #657180">发布时间：{{item.start}}</p>
          <Icon title="热度" type="md-eye" size="20" style="float: left;margin-left: 70%;margin-top: -3%;color: #657180"/>
          <p :title="item.hot" style="float: left;margin-left: 75%;margin-top: -3%;font-size: 15px;color: #657180">{{item.hot}}</p>
        </div>
        <Page @on-change="pageChange" :total="total" :page-size="num" show-elevator style="float: left;margin-left: 16%;margin-top: 6%;font-size: 16px"/>
      </div>
      <div style="float: left;width:30%;margin-left: 10%;margin-top: 50px">
        <h2 style="float: left;margin-left: 35%">最新通知:</h2>
        <div style="float:left;margin-left: 0%;margin-top: 2%;width:100%;">
        </div>
        <div v-for="item in hotArticleList" style="float: left;width:100%;">
          <h3 @click="jump(item.id)" style="float: left;margin-left: 3%;font-size: 20px;width:95%;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">{{item.title}}</h3>
          <Icon title="热度" type="md-eye" size="20" style="float: left;;margin-top: 2%;margin-left: 35%"/>
          <p title="热度" style="float: left;;margin-top: 2%;margin-left: 3%;font-size: 18px">{{item.hot}}</p>
        </div>
      </div>
    </div>
</template>

<script>
  import axios from 'axios'
  export default {

    created(){
      this.keyword = this.$route.params.keyword
      this.getCount(this.keyword)
      this.getData(0)
      this.getHotArticles()
    },
    data() {
      return{
        num:5,
        keyword:'',
        total:0,
        hotArticleList:[],
        articleList:[],
      }
    },
    methods:{
      jump(id){
        this.$router.push({
          name:'detail',
          params:{
            id:id
          }
        })
      },
      showDetail(id){
        this.$router.push({
          name:'detail',
          params:{
            id:id
          }
        })
    },

      change:function(keyword){
         //this.keyword = keyword
        this.getCount(keyword)
        //this.getData(0)
      },


      //标题点击事件
      jumpToDetail(id){
        this.$router.push({
          name:'detail',
          params:{
            id:id
          }
        })
      },
      pageChange(index){
        this.getData(index-1)
      },
      getCount(keyword){
        var self = this
        axios.get("/getTotalByKeyword/"+keyword)
          .then(function (response) {
            self.total = response.data
          })
      },
      getData(index){
        var self = this
        this.$http.get("/getArticlesByKeyword/"+this.keyword+"/"+index)
          .then(function (response) {
            self.articleList = response.data
          })
      },

      //获取热点文章
      getHotArticles(){
        var self = this
        this.$http.get("/getHotArticle/"+self.keyword)
          .then(function (response) {
            self.hotArticleList = response.data
          })
      }
    }
  }
</script>

<style scoped>
  .href{
    font-weight:bold;
    font-size: 20px;
    font-family: FangSun;
    color: #464c5b;
    text-decoration:none;
    cursor: pointer;
  }
  .href:hover{
    color: #fd4c5b;
  }
  .shadow{
    box-shadow: #666 1px 4px 10px;
    background: white;
  }
  .shadow:hover{
    background-color: rgba(0, 141, 225, 0.05);
  }
  .title{
    font-size: 18px;
    font-weight: bold;
    color: black;
    cursor: pointer;
  }
  h3{
    cursor: pointer;
  }
  h3:hover{
    color:#fd4c5b;
  }
</style>



