<template>
  <div style="float: left;margin-left: 5%;width:65%;margin-top: 2%">
    <Table border  :columns="columns" :data="message" style="width: 100%"></Table>
  </div>

</template>
<script>
  export default {

    created(){
      this.getMessage();
    },
    data () {
      return {
        columns: [
          {
            title: '活动',
            key: 'title',
            render: (h, params) => {
              var self =this
              return h('div', [
                  h('span', {
                    domProps: {
                      innerHTML: params.row.title
                    }
                  })
                ],
              );
            }
          },
          {
            title: '具体内容',
            key: 'content'
          },
          {
            title: '开始时间',
            key: 'start',
            sortable:true,
            width: 300,
            align: 'center',
          }
        ],
        message: []
      }
    },
    methods: {
      getMessage(){
        var id = this.$getCookie('userID');
        var self = this
        this.$http.get("/getMessage/"+id).then(function(response){
          self.message = response.data;
        })
      }
    }
  }
</script>



