import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import list from '@/components/List'

import header from '@/components/Header'
import usertag from '@/components/UserTag'
import message from '@/components/Message'
import detail from '@/components/Detail'
import concern from '@/components/Concern'
import collection from '@/components/ListPage'
import recommend from '@/components/Recommend'

import tagpage from '@/components/TagPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/tagpage',
      name: 'tagpage',
      component: tagpage
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/list',
      name: 'list',
      component: list
    },
    {
      path: '/usertag',
      name: 'usertag',
      component: usertag,
      redirect:'/collection',
      children:[
        {
          path: '/collection',
          name:'collection',
          component:collection
        },
        {
          path: '/concern',
          name:'concern',
          component:concern
        },
        {
          path: '/message',
          name: 'message',
          component: message
        },
        {
          path: '/recommend',
          name: 'recommend',
          component: recommend
        },
        {
          path:'*',
          redirect:'/collection'
        }
        ]
    },
    {
      path: '/header',
      name: 'header',
      component: header
    },
  ]
})



