<template>
    <div>
      <!--左边的文章详情-->
      <div style="width: 60%;float: left">
        <br>
        <br>
        <p style="font-size: 30px;margin-left: 10%;text-align: center">{{article.title}}</p>
        <p style="font-size: 20px;margin-left: 10%;width:90%;word-wrap:break-word;text-align: left">{{article.content}}</p>
        <div style="float: left;margin-left: 10%;width:90%">
          <Icon title="热度" type="md-eye" size="40" style="float: left;margin-left: 0%;margin-top: 0%;color: #657180"/>
          <p  style="float: left;margin-left: 3%;margin-top: 0%;font-size: 25px;color: #657180">{{article.hot}}</p>
          <Icon @click="addCollection" :type="iconType" title="添加收藏" size="40" style="float: left;margin-left:15%;margin-top: 0%;color:#ff5f17;cursor: pointer"/>
        </div>
        <div v-for="item in tags" style="float: left;margin-left: 10%;">
          <Tag @click.native="jumpByTag(item)" type="border" color="primary" style="float: left;margin-left: 5%;margin-top: 15%;font-size: 18px;width:100%">{{item}}</Tag>
        </div>
      </div>
      <!--右边的相关内容-->
      <div style="width: 30%;float: right;margin-right: 4%;margin-top: 2%" >
        <div style="background-color: rgb(245,245,245);height: 100px;border: 1px solid #b5b5b5" >
          <img src="../images/people.jpg" style="float: left;margin-left: 7%;margin-top: 10px;width:80px"/>
          <h3 @click="jumpByAuthor(article.author)" style="float: left;margin-left: 7%;margin-top: 4%;font-size: 20px">{{article.author}}</h3>
          <Button  type="error" style="float: left;margin-left: 25%;margin-top: 2%" @click="addInterset(article.author)" :disabled="flag">{{buttonContent}}</Button>
        </div>
        <br>
        <div>
          <h2 style="float: left;margin-left: 35%">最新通知:</h2>
          <div style="float:left;margin-left: 0%;margin-top: 2%;width:100%;">
          </div>
          <div v-for="item in articleList" style="float: left;width:100%;">
            <h3 @click="jump(item.id)" style="float: left;margin-left: 3%;font-size: 20px;width:95%;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">{{item.title}}</h3>
            <Icon title="热度" type="md-eye" size="20" style="float: left;;margin-top: 2%;margin-left: 35%"/>
            <p title="热度" style="float: left;;margin-top: 2%;margin-left: 3%;font-size: 18px">{{item.hot}}</p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      created(){
        //初始化得到id
        this.id = this.$route.params.id
        this.getDetailByID()
        this.getTagsByID()
      },

      data(){
        return {
          iconType:'md-heart-outline',
          buttonContent:"关注",
          flag:false,
          id:0,
          article:{},
          articleList:[
            {
              id:1,
              title:'12年手办官宣',
              count:2555
           },
            {
              id:2,
              title:'50年手办官宣',
              count:7825
            },
            {
              id:3,
              title:'70年手办官宣',
              count:60
            }],
          tags:[
          ]
        }
      },
      methods:{
        jumpByAuthor(author){
          this.$router.push({
            name:'tagpage',
            params:{
              keyword:author
            }

          })
        },

        jumpByTag(item){
          this.$router.push({
            name:'tagpage',
            params:{
              keyword:item
            }

          })
        },


        getTagsByID(){
          var self = this
          this.$http.get("/getArticleTags/"+this.id)
            .then(function (response) {
              self.tags = response.data
            })
        },

        //收藏
        addCollection(){
          var userID = this.$getCookie("userID")
          var self = this
          if(userID==null || userID==""){
            this.$Message.warning("请先登录")
            return
          }

          this.$http.post("/addCollection/"+userID+"/"+this.id).then(function(response){
            if(response.data == -1){
              self.$Message.warning("您已经收藏")
              self.iconType = 'md-heart'
            }else{
              self.iconType = 'md-heart'
              self.$Message.success("收藏成功")
            }
          })
        },
        jump(id){
          this.id = id
          this.getDetailByID()
          this.getTagsByID()
        },
        //订阅
        addInterset(author){
          var userID = this.$getCookie("userID")
          var self = this;
          if(userID==null || userID==""){
            this.$Message.warning("请先登录")
            return
          }
          this.$http.post("/addInterset/"+userID+"/"+author).then(function(response){
            if(response.data == -1){
              self.$Message.warning("您已经关注")
            }else{
              self.$Message.success("关注成功")
              self.buttonContent = "已关注"
              self.flag = true
            }
          })
        },

        getDetailByID(){
          var self = this
          this.$http.get("/getArticleDetail/"+this.id)
            .then(function (response) {
              self.article = response.data
              self.getTopFive()
            })
        },


        getTopFive(){
          var author = this.article.author
          var self = this
          this.$http.get("/getHotArticle/"+author)
            .then(function (response) {
              console.log(response.data)
               self.articleList = response.data
            })
        }
      },
      computed:{
          height(){
            return window.innerHeight+'px'
        }
      }
    }
</script>

<style scoped>
  h3{
    cursor: pointer;
  }
  h3:hover{
    color:#fd4c5b;
  }
</style>



